                <Language languageImage={franceFlag} languageName="Franais" languageText="Jai appris le franais car je compte faire ma vie  Paris. De plus, je trouve que cest une trs belle langue et surtout dune lgance ingalable ! Aprs tout, nous ne lappelons pas la  langue de lamour  pour rien."/>
